import refs from './utils/refs.js';
import Theme from './utils/themes';

export default function switchTheme() {
  if (refs.body.classList.contains(Theme.LIGHT)) {
    refs.body.classList.replace(Theme.LIGHT, Theme.DARK);
    localStorage.setItem('theme', Theme.DARK);
    refs.switch.setAttribute('checked', true);
  } else if (refs.body.classList.contains(Theme.DARK)) {
    refs.body.classList.replace(Theme.DARK, Theme.LIGHT);
    localStorage.setItem('theme', Theme.LIGHT);
    refs.switch.setAttribute('checked', false);
  }
}

refs.switch.addEventListener('change', switchTheme);
